import test from 'ava';
import mongoose from 'mongoose';
import { MongoDBServer } from 'mongomem';
import User from '../../src/user';

import {
  createMongoose
} from '../test_helper';

test.before('start server', async t => {
  console.log('await MongoDBServer.start()');
  await MongoDBServer.start();
})

test.beforeEach( async (t) => {
  console.log('createMongoose() before');
  const db = createMongoose();
  console.log('createMongoose() after');
  t.context.db = db;
});

test('Creating records ', async (t) => {
  console.log('Creating records');
  const { db } = t.context;
  const user_1 = new User({
    name: 'user_1',
  });
  console.log('user_1.save() before');
  const result = await user_1.save();
  console.log(result);
  console.log('user_1.save() afte');
  t.true( !user_1.isNew, 'user\'s `isNew` flag is false after a save' );
});
